.container {
  &__content {
    @include flexbox();
    height: 100vh;

    &--order {
      justify-content: flex-end;
    }

    &--gray {
      background-color: $admin_bcg;
      height: 100%;
      min-height: 100vh;
    }

    &--center {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__menu {
    width: 100%;
    height: 100%;
    min-width: $minwidth;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(21, 27, 31, 0.81);
    display: none;
    z-index: 999;
  }
}

.button {
  @include styletext(18px, $white, 21px);
  background: linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);
  font-family: "RobotoMd";
  background-blend-mode: darken;
  padding: 13.5px 0;
  border-radius: 8px;

  &:disabled {
    background: $light_gray;
  }
}

.burger {
  cursor: pointer;

  &:hover &__item {
    filter: brightness(0.6);
  }

  &__inner {
    width: 32px;
    height: 32px;
    position: relative;

    &--padding_t {
      @media (max-width: $bp2) {
        width: 42px;
        height: 32px;
      }

      @media (max-width: $bp3) {
        width: 32px;
      }
    }
  }

  &__item,
  &__item::before,
  &__item::after {
    height: 3px;
    background-color: $white;
    border-radius: 3px;
    width: 24px;
    position: absolute;

    @media (max-width: $bp2) {
      width: 32px;
    }

    @media (max-width: $bp3) {
      width: 19px;
    }
  }

  &__item {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &::before {
      content: "";
      top: -8px;
    }

    &::after {
      content: "";
      top: 8px;
    }

    &--border_bcg,
    &--border_bcg::before,
    &--border_bcg::after {
      background-color: $main_black;
      width: 19px;
    }

    &--close {
      background-color: transparent;

      &::before {
        top: 0;
        transform: rotate(45deg);
      }

      &::after {
        top: 0;
        transform: rotate(-45deg);
      }
    }
  }
}

.sidebar {
  // background-color: $sidebar_bcg;

  @media (max-width: $bp3) {
    display: none;
  }

  &--fixed {
    position: fixed;
    height: 100%;
    top: 0;
    left: 0;
  }

  &__inner {
    width: 64px;
    height: 100%;
    padding: 32px 0 15.5px;
    @include flexbox();
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: $sidebar_bcg;

    @media (max-width: $bp1) {
      width: 65px;
    }

    @media (max-width: $bp2) {
      width: 86px;
    }
  }

  &-lang-switch {
    display: block;

    &__display {
      display: none;

      @media (max-width: $bp3) {
        display: block;
      }
    }

    &__button {
      @include styletext(13px, $main_accent, 15px);
      font-weight: bold;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      background-color: transparent;
      width: 48px;
      height: 48px;

      &:hover {
        color: #fff;
        border: 1px solid #fff;
        border-radius: 50%;
      }

      &:active {
        border: 1px solid #fff;
        border-radius: 50%;
        color: $main_accent;
      }
    }
  }
}

.body {
  width: 50%;

  @media (max-width: $bp1) {
    width: 57.3%;
  }

  @media (max-width: $bp2) {
    width: 100%;
  }

  &__inner {
    height: 100%;
    padding: 32px 64px;
    @include flexbox();
    flex-direction: column;
    justify-content: space-between;

    @media (max-width: $bp1) {
      padding: 32px;
    }

    @media (max-width: $bp3) {
      padding: 0;
    }
  }

  &-header {
    &__inner {
      @extend %flexinitial;

      @media (max-width: $bp3) {
        display: block;
      }
    }

    &__containerXS {
      @media (max-width: $bp3) {
        @include flexbox;
        justify-content: space-between;
        align-items: flex-start;
        padding: 16px 16px 0 16px;
      }
    }

    &__logo {
      @include styletext(30px, $main_accent, 35px);
      font-weight: bold;
    }

    &__location {
      @include flexbox();
      align-items: center;
      cursor: pointer;

      @media (max-width: $bp3) {
        justify-content: flex-end;
        margin-top: 10px;
      }
    }

    &__icon {
    }

    &__text {
      @include styletext(14px, $dark_gray, 16px);
      margin-left: 7px;
    }

    &-burger {
      display: none;

      @media (max-width: $bp3) {
        display: block;
      }

      &__display {
        display: none;
      }
    }
  }

  &-content {
    &__inner {
      margin-top: 10px;
      @media (max-width: $bp3) {
        padding: 0 16px;
      }
    }

    &__title {
      @include styletext(70px, $main_black, 66px);
      font-weight: bold;

      @media (max-width: $bp3) {
        font-size: 32px;
        line-height: 100%;
      }
    }

    &__subtitle {
      @include styletext(70px, $main_accent, 66px);
      font-weight: bold;
      margin-top: 5px;

      @media (max-width: $bp3) {
        font-size: 32px;
        line-height: 100%;
      }
    }

    &__text {
      @include styletext(26px, $dark_gray, 30px);
      font-family: "RobotoL";
      margin-top: 25px;

      @media (max-width: $bp3) {
        font-size: 18px;
        line-height: 21px;
        margin-top: 16px;
      }
    }

    &__button {
      width: 250px;
      margin-top: 60px;

      &:hover {
        background: linear-gradient(90deg, #0b934a 2.61%, #026e47 112.6%);
      }

      &:active {
        background: linear-gradient(90deg, #076432 2.61%, #013c27 112.6%);
      }

      @media (max-width: $bp3) {
        width: 100%;
        margin-top: 32px;
        border-radius: 0;
      }
    }
  }

  &-footer {
    &__inner {
      @extend %flexinitial;
      margin-top: 10px;

      @media (max-width: $bp3) {
        background-color: $sidebar_bcg;
        padding: 16px;
        flex-direction: column;
        align-items: flex-end;
      }
    }

    &__copy {
      @include styletext(13px, $dark_gray, 15px);
      @media (max-width: $bp3) {
        color: $light_gray;
        order: 2;
        margin-top: 8px;
      }
    }

    &__phone {
      @include styletext(13px, $main_black, 15px);

      &:hover,
      &:active {
        color: $main_accent;
      }

      @media (max-width: $bp3) {
        color: $main_accent;
        order: 1;

        &:hover {
          color: $light_gray;
        }

        &:active {
          color: $white;
        }
      }
    }
  }
}

.slider {
  &__bcg {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
    mix-blend-mode: normal;
    width: 100%;
    height: 100%;
  }

  &__item {
    width: 100%;
    height: 100%;
  }

  &__image {
    width: 100%;
    height: 100%;

    &--slide1 {
      background: url("../images/startscreen/slider_screen_1.jpg") center
        no-repeat;
      background-size: cover;
    }

    &--slide2 {
      background: url("../images/startscreen/slider_screen_2.jpg") 5% 50%
        no-repeat;
      background-size: cover;
    }

    &--slide3 {
      background: url("../images/startscreen/slider_screen_3.jpg") left
        no-repeat;
      background-size: cover;
    }

    &--slide4 {
      background: url("../images/startscreen/slider_screen_4.jpg") center
        no-repeat;
      background-size: cover;
    }
  }

  &-content {
    height: 100%;
    @include flexbox();
    // align-items: flex-end;
    align-items: center;
    &__top {
      // height: calc(100% - 237px);
      // @include flexbox();
      padding: 0 110px 0 96px;
      // flex-direction: column;
      // justify-content: flex-start;

      @media (max-width: $bp1) {
        padding: 0 68px 0 64px;
        // height: calc(100% - 277px);
      }
    }

    &__title {
      @include styletext(40px, $white, 47px);
      font-family: "RobotoMd";

      @media (max-width: $bp1) {
        font-size: 32px;
        line-height: 37px;
      }
    }

    &__text {
      @include styletext(24px, $light_gray, 90%);
      font-family: "RobotoL";
      margin-top: 8px;

      @media (max-width: $bp1) {
        font-size: 22px;
        line-height: 100%;
      }
    }

    &__button {
      width: 164px;
      border-radius: 4px;
      margin-top: 32px;
      color: $light_gray;

      &--bcg1 {
        background: linear-gradient(90deg, #13493f 0%, #0c7b1b 100%);

        &:hover {
          background: linear-gradient(90deg, #0f3831 0%, #0a6116 100%);
        }

        &:active {
          background: linear-gradient(90deg, #0d302a 0%, #07410e 100%);
        }
      }

      &--bcg2 {
        background: linear-gradient(90deg, #132949 0%, #0c7b67 100%);

        &:hover {
          background: linear-gradient(90deg, #11233f 0%, #0a5e4e 100%);
        }

        &:active {
          background: linear-gradient(90deg, #0e1c33 0%, #07463a 100%);
        }
      }

      &--bcg3 {
        background: linear-gradient(90deg, #493013 0%, #7b0c3b 100%);

        &:hover {
          background: linear-gradient(90deg, #38250f 0%, #660b31 100%);
        }

        &:active {
          background: linear-gradient(90deg, #291b0b 0%, #4e0926 100%);
        }
      }

      &--bcg4 {
        background: linear-gradient(90deg, #281349 0%, #720c7b 100%);

        &:hover {
          background: linear-gradient(90deg, #1d0e36 0%, #57095e 100%);
        }

        &:active {
          background: linear-gradient(90deg, #180b2c 0%, #420747 100%);
        }
      }
    }
  }
}

/* Слайдер */
.slick-slider {
  width: calc(50% - 32px);
  position: relative;

  @media (max-width: $bp1) {
    width: calc(42.7% - 27.5px);
  }

  @media (max-width: $bp2) {
    display: none;
  }
}
/* Слайдер запущен */
.slick-slider.slick-initialized {
}
/* Слайдер с точками */
.slick-slider.slick-dotted {
}
/* Ограничивающая оболочка */
.slick-list {
  height: 100%;
  overflow: hidden;
}
/* Лента слайдов */
.slick-track {
  display: flex;
  height: 100%;
}
/* Слайд */
.slick-slide {
}
/* Слайд активный (показывается) */
.slick-slide.slick-active {
}
/* Слайд основной */
.slick-slide.slick-current {
}
/* Слайд по центру */
.slick-slide.slick-center {
}
/* Клонированный слайд */
.slick-slide.slick-cloned {
}
/* Стрелка */
.slick-arrow {
  height: 100%;
  width: 64px;
  position: absolute;
  top: 0;
  font-size: 0;
  z-index: 10;
  background-color: transparent;

  &-img {
    margin: 0 auto;
  }

  &:hover {
    background-color: rgba(14, 194, 97, 0.2);
  }

  @media (max-width: $bp1) {
    width: 32px;
  }
}
/* Стрелка влево */
.slick-arrow.slick-prev {
  left: 0;
}
/* Стрелка вправо */
.slick-arrow.slick-next {
  right: 0;
}
/* Стрелка не активная */
.slick-arrow.slick-disabled {
}
/* Точки (булиты) */
.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 32px;
  left: 50%;
  transform: translateX(-50%);
}
.slick-dots li {
  list-style: none;
}
/* Активная точка */
.slick-dots li.slick-active button {
  background-color: $main_accent;
}
/* Элемент точки */
.slick-dots li button {
  font-size: 0;
  width: 8px;
  height: 8px;
  background-color: $light_gray;
  border-radius: 50%;
  margin: 0 4px;
}

.menu {
  height: 100%;

  &__inner {
    height: 100%;
    width: calc(50% + 32px);
    background-color: $sidebar_bcg;
    padding: 32px 0 16px 16px;
    @include flexbox();
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    @media (max-width: $bp1) {
      width: calc(57.3% + 28px);
    }

    @media (max-width: $bp2) {
      width: 100%;
      padding-left: 22px;
    }

    @media (max-width: $bp3) {
      padding: 16px 0 16px 16px;
    }
  }
}

.list {
  &--flex {
    @include flexbox();
    margin-top: 38px;
  }

  &__inner {
    padding-left: 112px;
    height: 100%;
    @include flexbox();
    flex-direction: column;
    justify-content: center;

    @media (max-width: $bp1) {
      padding-left: 81px;
    }

    @media (max-width: $bp3) {
      padding-left: 10px;
      justify-content: flex-start;
      margin-top: 28px;
    }
  }

  &__item {
    @include styletext(32px, $white, 37px);
    font-family: "RobotoMd";
    text-transform: uppercase;
    margin-top: 10px;

    @media (max-width: $bp2) {
      font-size: 28px;
      line-height: 33px;
    }

    @media (max-width: $bp3) {
      font-size: 22px;
      line-height: 26px;
    }

    &:first-child {
      margin-top: 0;
    }

    &:hover {
      color: $main_accent;
    }

    &--social {
      margin: 0 0 0 24px;
      line-height: 0;

      &:first-child {
        margin: 0;
      }
    }
  }

  &__link {
    display: block;
  }

  &__image:hover path {
    fill: $main_accent;
  }
}
